import _ from 'lodash';

export default {
    props: {
        apiUrl: {
            type: String,
            default: '',
            required: false,
        },
    },
    data: function() {
        return {
            data: [],
            dataApiUrl: '',
            loading: false,
            pagination: {
                page: 0,
                per_page: 10,
            },
            getDataOnCreate: true,
            withoutPagination: false,
        };
    },
    created() {
        if (this.getDataOnCreate) {
            this.getData();
        }
    },
    watch: {
        pagination: {
            deep: true,
            handler() {
                this.getData();
            },
        },
    },
    methods: {
        appendParams(params, obj) {
            for (let key in obj) {
                if (obj.hasOwnProperty(key)) {
                    params.append(key, obj[key]);
                }
            }
        },
        setPagination() {
            let app = this;
            if (app.withoutPagination !== true) {
                let pagination = {
                    page: app.pagination.page,
                    per_page: app.pagination.per_page,
                };
                return pagination;
            }
            return false;
        },
        setQuery() {
            return false;
        },
        beforeGetData() {
            return true;
        },
        afterGetData() {
            this.$emit('afterGetData', this.data);
        },
        getData: _.debounce(function() {
            let app = this;
            return new Promise(resolve => {
                if (app.getApiUrl) {
                    if (app.beforeGetData()) {
                        app.loading = true;
                        let params = new URLSearchParams();

                        let pagination = app.setPagination();
                        let query = app.setQuery();

                        if (pagination) {
                            app.appendParams(params, pagination);
                        }

                        if (query) {
                            app.appendParams(params, query);
                        }

                        app.axios
                            .get(
                                app.$api + app.getApiUrl,
                                params ? { params: params } : '',
                            )
                            .catch(function(error) {
                                throw new Error(error);
                            })
                            .then(function(response) {
                                app.$set(app, 'data', response.data);
                                app.afterGetData();
                                resolve(app.data);
                                app.loading = false;
                            });
                    }
                }
            });
        }, 1000),
    },
    computed: {
        getApiUrl() {
            return this.dataApiUrl
                ? this.dataApiUrl
                : this.apiUrl
                ? this.apiUrl
                : '';
        },
    },
};
