<template>
    <transition name="fade"
        mode="out-in">
        <div v-if="loading"
            class="app-preloader"
            :class="classes"
            :key="1">
            <div class="app-preloader__icon">
                <div class="preloader">
                </div>
            </div>
        </div>
        <template v-else-if="this.$slots.default">
            <slot></slot>
        </template>
    </transition>
</template>

<script>
export default {
    name: "AppPreloader",
    props: {
        loading: {
            type: Boolean,
            default: true
        },
        inComponent: Boolean
    },
    computed: {
        classes() {
            return {
                "app-preloader--in-component": this.inComponent
            };
        }
    }
};
</script>