<template>

    <div class="home">
        <div class="app-actions">
            <div class="app-actions__search app-search">
                <input class="app-search__input"
                    v-model="searchQuery"
                    type="text"
                    placeholder="Enter repository name"
                    :readonly="loading">
            </div>
            <button class="app-actions__update-btn app-btn"
                @click="updateData">
                <span v-if="loading">Loading</span>
                <span v-else>Update</span>
            </button>
        </div>
        <div class="app-repositories">
            <app-preloader in-component
                :loading="loading"></app-preloader>
            <template v-if="isDataItems">
                <template v-for="(item, index) in data.items">
                    <div class="app-repositories__item app-repository"
                        :key="index">
                        <a :href="item.html_url"
                            class="app-repository__name">
                            {{item.name}}
                        </a>
                        <div v-if="item.description"
                            class="app-repository__decription">
                            {{item.description}}
                        </div>
                        <div class="app-repository__user app-user">
                            <div class="app-user__image">
                                <img :alt="item.login"
                                    :src="item.owner.avatar_url">
                            </div>
                            <a :href="item.owner.html_url"
                                class="app-user__name">
                                {{item.owner.login}}
                            </a>
                        </div>
                        <div class="app-repository__info app-repository-info">
                            <div v-if="item.language"
                                class="app-repository-info__label app-repository-info__label--language app-label">
                                <span class="app-label__name">Language:</span>
                                <span class="app-label__value">{{item.language}}</span>
                            </div>
                            <div class="app-repository-info__label app-repository-info__label--starts app-label">
                                <span class="app-label__name">Stars:</span>
                                <span class="app-label__value">{{item.stargazers_count}}</span>
                            </div>
                            <div class="app-repository-info__label app-repository-info__label--forks app-label">
                                <span class="app-label__name">Forks:</span>
                                <span class="app-label__value">{{item.forks}}</span>
                            </div>
                            <div class="app-repository-info__label app-repository-info__label--updated app-label">
                                <span class="app-label__name">Updated:</span>
                                <span class="app-label__value">{{getFormattedDate(item.updated_at)}}</span>
                            </div>
                        </div>
                    </div>
                </template>
            </template>
            <template v-else>
                <div class="app-repositories__empty">No repositories.</div>
            </template>
        </div>
    </div>

</template>

<script>
import basicComponentsLogic from "@/mixins/basicComponentsLogic";

export default {
    name: "Home",
    data: function() {
        return {
            loading: false,
            searchQuery: this.getSearchQuery,
            dataApiUrl: "search/repositories",
            getDataOnCreate: false
        };
    },
    created() {
        let app = this;
        if (app.getSearchQuery) {
            app.searchQuery = app.getSearchQuery;
            app.getDataOnCreate = true;
        }
    },
    watch: {
        searchQuery() {
            localStorage.setItem("searchQuery", this.searchQuery);
            this.updateData();
        }
    },
    methods: {
        beforeGetData() {
            if (this.searchQuery) {
                return true;
            }
            this.data = "";
            return false;
        },
        updateData() {
            let app = this;
            app.pagination.per_page = 10;
            app.getData();
        },
        setQuery() {
            let app = this;
            let searchQuery = {
                q: app.searchQuery,
                sort: "updated",
                order: "desc"
            };
            return searchQuery;
        },
        getFormattedDate(date) {
            let newDate = new Date(date);

            let padStartDateValue = function(value) {
                return value.toString().padStart(2, "0");
            };

            let year = newDate.getFullYear();
            let month = padStartDateValue(1 + newDate.getMonth());
            let day = padStartDateValue(newDate.getDate());
            let hourse = padStartDateValue(newDate.getHours());
            let minutes = padStartDateValue(newDate.getMinutes());

            return `${month}/${day}/${year} ${hourse}:${minutes}`;
        }
    },
    computed: {
        getSearchQuery() {
            return localStorage.getItem("searchQuery")
                ? localStorage.getItem("searchQuery")
                : "";
        },
        isDataItems() {
            return this.data.items && this.data.items.length > 0 ? true : false;
        }
    },
    mixins: [basicComponentsLogic]
};
</script>
